.cart {
	padding-top: 50px;
	margin-bottom: 80px;
	@include until-tablet {
		margin-bottom: 45px; }
	&__empty {
		margin: 100px 0;
		display: flex;
		flex-direction: column;
		& span {
			text-align: center;
			font-weight: bold;
			font-size: 24px;
			margin-bottom: 30px; } }
	&__title {
		&-block {
			margin-bottom: 35px;
			width: 100%;
			display: flex;
			align-items: center;
			justify-content: space-between; } }
	&__delete-all {
		display: flex;
		align-items: center;
		flex-shrink: 0;
		&-button {
			margin-left: 10px;
			width: 20px;
			height: 20px;
			border-radius: 50%;
			display: flex;
			align-items: center;
			justify-content: center;
			background-color: #3C3C3B; }
		&:hover a {
			border-bottom-color: transparent; }
		& a {
			border-bottom: 1px dashed rgba(60, 60, 59, 0.5);
			transition: border-bottom-color .3s; }
		& svg {
			flex-shrink: 0;
			width: 10px;
			height: 10px; } }
	&__list {
		border-collapse: collapse;
		width: 100%;
		// border-spacing: 0 20px
		&-header {
			& th {
				text-align: left;
				font-weight: bold;
				font-size: 14px;
				line-height: 120%;
				color: #B1B2B3;
				padding: 10px; } } }
	&__item {
		width: 100%;
		// display: flex
		// justify-content: space-between
		// align-items: center
		background-color: #fff;
		padding: 17px;
		border: 1px solid #EAEAEA;
		border-radius: 5px;
		&:not(:last-of-type) {
			margin-bottom: 20px; }
		& td {
			padding: 10px; }
		&-product {
			& > div {
				display: flex;
				align-items: center; }
			&-img {
				margin-right: 24px;
				max-width: 170px;
				& img {
					width: 100%;
					height: auto; } }
			&-info {
				display: flex;
				flex-direction: column; }
			&-title {
				font-weight: bold;
				font-size: 18px;
				line-height: 120%;
				padding-bottom: 2px;
				text-transform: uppercase;
				margin-bottom: 10px;
				transition: border-bottom-color .3s;
				border-bottom: 1px dashed rgba(60, 60, 59, 0.5);
				&:hover {
					border-bottom-color: transparent; } }
			&-art {
				font-weight: bold;
				color: #B1B2B3; } }
		&-price {
			font-weight: bold;
			font-size: 24px; }
		&-total-price {
			font-weight: bold;
			font-size: 24px; }
		&-delete {
			border-radius: 50%;
			background-color: $red;
			width: 35px;
			height: 35px;
			display: flex;
			align-items: center;
			justify-content: center;
			cursor: pointer;
			transition: background-color .3s;
			&:hover {
				background-color: #ca0202; }
			& svg {
				width: 15px;
				height: 15px; } } }
	&__total {
		background: #F6F6F6;
		width: 100%;
		margin-top: 20px;
		display: flex;
		align-items: center;
		justify-content: flex-end;
		padding: 16px;
		margin-bottom: 40px;
		position: relative;
		&::after {
			content: '';
			position: absolute;
			left: 0;
			top: 100%;
			width: 100%;
			height: 11px;
			background: url('../img/svg-sprite/triangle.svg');
			background-repeat: repeat no-repeat; }

		&-content {
			display: flex;
			align-items: flex-start;
			flex-direction: column;
			margin-right: 130px; }
		&-print {
			cursor: pointer;
			padding-bottom: 3px;
			display: flex;
			align-items: center;
			margin-bottom: 20px;
			transition: border-bottom-color .3s;
			border-bottom: 1px dashed rgba(60, 60, 59, 0.5);
			&:hover {
				border-bottom-color: transparent; }
			& svg {
				width: 13px;
				height: 15px;
				margin-right: 10px; } }
		&-price {
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			& b {
				text-transform: uppercase;
				margin-bottom: 10px; }
			& span {
				font-weight: bold;
				font-size: 24px; }
			& p {
				margin-top: 15px; } } }
	&__buttons {
		width: 100%;
		display: flex;
		justify-content: space-between;
		align-items: center; } }

.quantity-block {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	& input {
		font-weight: bold;
		font-size: 24px;
		text-align: center;
		background: #F6F6F6;
		border: 1px solid #EAEAEA;
		height: 50px;
		padding: 0;
		display: flex;
		justify-content: center;
		appearance: none;
		width: 85px; }
	& button {
		height: 50px;
		display: flex;
		align-items: center;
		justify-content: center;
		width: 31px;
		font-weight: 300;
		font-size: 24px;
		color: #B1B2B3;
		border: 1px solid #EAEAEA;
		box-sizing: border-box;
		border-radius: 0px 2px 2px 0;
		transition: color .3s, background-color .3s;
		line-height: 1;
		&:hover {
			background-color: #EAEAEA;
			color: $orange; } } }
