.footer__social > span {
  display: block;
  font-weight: 700;
  margin-bottom: 10px;
}

.footer__social-list {
  display: flex;
  align-items: center;
}

.footer__social-list svg {
  width: 26px;
  height: 26px;
  fill: #3C3C3B;
  transition: fill .3s;
}

.footer__social-list svg:hover {
  fill: #575756;
}

.footer__social-list a {
  display: block;
}

.footer__social-list a:not(:last-of-type) {
  margin-right: 10px;
}

.footer__payment {
  margin-bottom: 20px;
}

.footer__payment > span {
  display: block;
  font-weight: 700;
  margin-bottom: 10px;
}

.footer__payment-list {
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer__payment-list img:not(:last-of-type) {
  margin-right: 10px;
}

.footer__policy {
  border-bottom: 1px dashed;
  width: -webkit-max-content;
  width: max-content;
  transition: border-bottom-color .3s;
}

.footer__policy:hover {
  border-bottom-color: transparent;
}

.footer__logo {
  display: block;
  max-width: 135px;
  height: 88px;
  margin-bottom: 25px;
}

.footer__logo svg {
  width: 100%;
  height: 100%;
}

.footer__about {
  text-align: center;
}

.footer__nav-block {
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
}

.footer__nav-block:not(:last-of-type) {
  margin-right: 50px;
}

.footer__nav-block-title {
  font-weight: 700;
  margin-bottom: 15px;
}

.footer__nav-item a {
  border-bottom: 1px dashed;
  transition: border-bottom-color .3s;
}

.footer__nav-item:hover a {
  border-bottom-color: transparent;
}

.footer__nav-item:not(:last-of-type) {
  margin-bottom: 10px;
}

@media (max-width: 1024px) {
  .content-block .table-container {
    overflow-x: auto;
  }
}

@media screen and (max-width: 1023px) {
  .sidebar {
    display: none;
  }

  .header__nav-item:not(:last-of-type) {
    margin-right: 30px;
  }

  .catalog__cards {
    margin-bottom: 40px;
  }
}

@media screen and (max-width: 768px) {
  .content-block {
    margin-bottom: 45px;
  }

  .catalog {
    margin-bottom: 45px;
  }

  .banner {
    margin-bottom: 45px;
  }

  .promo {
    margin-bottom: 45px;
  }

  .promo__top {
    margin-bottom: 30px;
  }

  .product-slider {
    margin-bottom: 45px;
  }

  .product-card {
    margin-bottom: 45px;
  }

  .stocks {
    margin-bottom: 45px;
  }

  .advantages {
    margin-bottom: 45px;
  }

  .clients {
    margin-bottom: 45px;
  }

  .map {
    margin-bottom: 45px;
  }

  .office {
    margin-bottom: 45px;
  }

  .requisites {
    margin-bottom: 45px;
  }

  .contacts {
    margin-bottom: 45px;
  }

  .delivery {
    margin-bottom: 45px;
  }

  .payment {
    margin-bottom: 45px;
  }

  .catalog-page {
    margin-bottom: 45px;
  }

  .cart {
    margin-bottom: 45px;
  }

  .questions {
    margin-bottom: 45px;
  }
}

@media (max-width: 767px) {
  .content-block ul {
    font-size: 14px;
    line-height: 24px;
  }

  .content-block ol {
    font-size: 14px;
    line-height: 24px;
  }

  .content-block .table-container table th {
    padding: 10px 15px;
  }

  .content-block .table-container table td {
    padding: 10px 15px;
  }
}

@media (max-width:576px) {
  .fancybox-thumbs {
    width: 110px;
  }

  .fancybox-show-thumbs .fancybox-inner {
    right: 110px;
  }

  .fancybox-thumbs__list a {
    max-width: calc(100% - 10px);
  }
}

@media (max-height:576px) {
  .fancybox-slide {
    padding-left: 6px;
    padding-right: 6px;
  }

  .fancybox-slide--image {
    padding: 6px 0;
  }

  .fancybox-close-small {
    right: -6px;
  }

  .fancybox-slide--image .fancybox-close-small {
    background: #4e4e4e;
    color: #f2f4f6;
    height: 36px;
    opacity: 1;
    padding: 6px;
    right: 0;
    top: 0;
    width: 36px;
  }

  .fancybox-caption {
    padding-left: 12px;
    padding-right: 12px;
  }

@supports (padding:max(0px)) {
    .fancybox-caption {
      padding-left: max(12px,env(safe-area-inset-left));
      padding-right: max(12px,env(safe-area-inset-right));
    }
}
}

@media print, (prefers-reduced-motion: reduce) {
  .animate__animated {
    animation-duration: 1ms !important;
    transition-duration: 1ms !important;
    animation-iteration-count: 1 !important;
  }

  .animate__animated[class*='Out'] {
    opacity: 0;
  }
}